import axios from "@nextcloud/axios";
import { generateUrl } from "@nextcloud/router";
export default {
  name: "ServerEditor",
  props: {
    id: {
      default: null,
      type: Number
    },
    name: {
      required: false,
      type: String
    },
    publishurl: {
      required: false,
      type: String
    },
    maxuploads: {
      default: 5,
      type: Number
    },
    maxuploadfilesize: {
      default: 512,
      type: Number
    },
    checkssl: {
      default: false,
      type: Boolean
    }
  },
  model: {
    event: 'server-change'
  },
  data() {
    return {
      mutable_name: this.name,
      mutable_publishUrl: this.publishurl,
      mutable_maxUploads: this.maxuploads,
      mutable_maxUploadFilesize: this.maxuploadfilesize,
      mutable_checkSsl: this.checkssl
    };
  },
  methods: {
    resetProps() {
      this.mutable_name = null;
      this.mutable_publishUrl = null;
      this.mutable_maxUploads = 5;
      this.mutable_maxUploadFilesize = 512;
      this.mutable_checkSsl = false;
    },
    hasChanged() {
      return this.mutable_name !== this.name || this.mutable_publishUrl !== this.publishurl || this.mutable_maxUploads !== this.maxuploads || this.mutable_maxUploadFilesize !== this.maxuploadfilesize || this.mutable_checkSsl !== this.checkssl;
    },
    saveServer() {
      let data = {};
      if (this.id) {
        data["id"] = this.id;
      }
      data["name"] = this.mutable_name;
      data["publishUrl"] = this.mutable_publishUrl;
      data["maxUploads"] = this.mutable_maxUploads;
      data["maxUploadFilesize"] = this.mutable_maxUploadFilesize;
      data["checkSsl"] = this.mutable_checkSsl;
      console.debug(JSON.stringify(data));
      axios.post(generateUrl('/apps/b2sharebridge/server'), {
        server: data
      }).then(response => {
        this.$emit('server-change', this.id === null ? 0 : this.id);
      }).catch(error => {
        console.error("Could not save server");
        console.debug(error);
      });
    },
    deleteServer() {
      if (this.id) {
        axios.delete(generateUrl('/apps/b2sharebridge/servers/' + this.id)).then(response => {
          console.log("Deleted server '" + this.name + "'");
          this.$emit('server-change', this.id);
        }).catch(error => {
          console.error("Could not delete server");
          console.debug(error);
        });
      }
    }
  },
  computed: {
    getCheckboxName() {
      return "checkSsl" + (this.id === null ? "" : this.id);
    }
  }
};